import * as e from 'cors';
import { KeyValue } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { listas } from 'src/app/models/back-service.model';
import { co, cp } from '@fullcalendar/core/internal-common';
import { IDropdownSettings } from 'ng-multiselect-dropdown';
import { Promotion } from 'src/app/models/promotions-models';
import { UtilsService } from 'src/app/services/utils.service';
import { CompanyService } from 'src/app/services/company.service';
import { PromotionsService } from 'src/app/services/promotions.service';

@Component({
  selector: 'app-admin-promociones',
  templateUrl: './admin-promociones.component.html',
  styleUrls: ['./admin-promociones.component.css']
})
export class AdminPromocionesComponent implements OnInit {
  // public filtro = '';
  public count: number = 1500;
  public loading: boolean = false;
  public empresasAll: any[] | any = [];
  public empresasAux: any[] | any = [];
  public currentCompanyId: number | boolean = false;
  public userRole: string | boolean = false; // sessionStorage.getItem('Role')?.toString()!;
  public userCompanyId: number | boolean = false; //Number(sessionStorage.getItem('companyId')?.toString()!) || undefined;

  // public selectedItems: any[] = [{ id: 1, name: 'Comercio por defecto' }];
  // public selectedCompany: number = 0;
  public selectedCompany: any;
  public empresas: any[] = [{ id: null, nombreComercial: 'Seleccione...' }];
  // public empresas: any[] | any // = [{}] // [{ id: null, nombreComercial: 'Seleccione...' }];
  // public selectedCompany: any[] = [{}] // [{ id: null, nombreComercial: 'Seleccione una empresa...' }]

  public listHeading: {}[] = [
    {
      'Categoría ': '',
      Descripción: '',
      'Fecha de creación': '',
      'Fecha de finalización': '',
      Estatus: '',
      '': '',
    },
  ];

  public dropdownSettings: IDropdownSettings = {
    idField: 'id',
    itemsShowLimit: 1,
    singleSelection: true,
    allowSearchFilter: true,
    textField: 'nombreComercial',
    allowRemoteDataSearch: false,
    closeDropDownOnSelection: true,
    searchPlaceholderText: 'Buscar'
  };

  constructor(
    private utilsService: UtilsService,
    private companyService: CompanyService,
    private promotionService: PromotionsService
  ) {
    this.categoriesHeading = Object.keys(this.listHeading[0]);
  }

  renderList: any[] = [];
  isShowDiv: boolean = true;
  categoriesHeading: string[] = [];

  // promotionList: Promotion[] = [];
  accessToken: string = sessionStorage.getItem('Token')?.toString()!;
  promotionsList: Promotion[] = [];
  public isAdmin: boolean = true;
  totalPages = 0;
  pageSize = 15;
  pageNum = 0;
  isBackAction: boolean = false;

  ngOnInit(): void {
    // this.limpiar();
    this.loadData();

    this.userRole = sessionStorage.getItem('Role')?.toString()!;

    const companyID = sessionStorage.getItem('companyId')?.toString();
    companyID != 'null' ? this.userCompanyId = Number(companyID) : this.userCompanyId = false;

    const currentCompanyID = this.utilsService.getParam('companyId');
    currentCompanyID != false ? this.currentCompanyId = Number(currentCompanyID) : this.currentCompanyId = currentCompanyID;

    if (this.currentCompanyId != false) {
      this.isBackAction = true;
    }
  }

  originalOrder = (
    a: KeyValue<string, string>,
    b: KeyValue<string, string>
  ): number => {
    return 0;
  };


  async onFilter($event: any) {
    const value: string = $event.toLowerCase();
    if (value.length >= 3) {
      this.empresas = await this.empresasAll.filter((d: { nombreComercial: any; }, index: any) => {
        if (d.nombreComercial !== null) {
          if (d.nombreComercial.toLowerCase().indexOf(value) !== -1) {
            return true;
          }
        }
        return false;
      })
    }
  }

  onItemSelect(item: any) {
    // this.selectedCompany = item;
    // this.selectedCompany = item.id;
  }

  onDeSelect(item: any) {
    this.limpiar();
  }

  onDropDownClose() {
    this.empresas = this.empresasAux;
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if ('item' + index !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }


  async loadData() {
    this.loading = true;

    const countCompany = await this.companyService.getListCompany(0, this.count);
    countCompany.success ? this.count = countCompany.count : this.count = 10000;

    this.companyService.getListCompany(0, this.count).then(
      async response => {
        this.empresasAll = response.result || []; //response.result.rows;
        this.empresas = countCompany.result || []; //countCompany.result.rows;
        this.empresasAux = countCompany.result || []; //countCompany.result.rows;
        // console.log(this.empresas);      

        if (this.userRole === 'ROLE_ALIADO_COMERCIAL' && this.userCompanyId != false) {
          this.isAdmin = false;
          const empresa = await this.empresasAll.find((e: any) => e.id === Number(this.userCompanyId))
          this.selectedCompany = [empresa];
          await this.loadCompanyPromotions();
        }
        else if (this.currentCompanyId != false) {
          const empresa = await this.empresasAll.find((e: any) => e.id === Number(this.currentCompanyId))
          this.selectedCompany = [empresa];
          await this.loadCompanyPromotions();
        }
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.message.descripcion);
      }
    ).finally(() => {
      this.loading = false;
    });
  }

  async loadCompanyPromotions() {
    this.loading = true;
    this.renderList = [];
    try {
      if (this.isAdmin) {
        var response = await this.promotionService.getAllPromotions(
          '',
          null,
          this.pageNum,
          this.pageSize,
          null,
          null,
          this.selectedCompany[0].id!.toString()
        );
      } else {
        var response =
          await this.promotionService.getAllPromotionsCommercialRole(
            '',
            null,
            this.pageNum,
            this.pageSize,
            null,
            null,
            this.selectedCompany[0].id!
          );
      }

      if (response?.success === true) {
        this.renderList = [];
        if (this.isAdmin) {
          this.renderList = response.result.map((e: any) => {
            return {
              categoría: e.categoryName,
              descripción: e.description,
              'fecha de creación': e.startDate,
              'fecha de finalización': e.finishDate,
              estatus: e.status,
              id: e.id
            }
          });
          this.totalPages = Math.ceil(response.count / this.pageSize);
        } else {                
          this.renderList = response.result['rows'].map((e: any) => {
            return {
              categoría: e.categoryName,
              descripción: e.description,
              'fecha de creación': e.startDate,
              'fecha de finalización': e.finishDate,
              estatus: e.status,
              id: e.id
            }
          });
          this.totalPages = Math.ceil(response.result.count / this.pageSize);
        }
      } else {
        this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
      }
    } catch (error) {
      if (!this.isBackAction) {
        this.utilsService.dialog('ERROR', 'Error', 'No existen promociones');
      } else {
        this.limpiar();
        this.renderList = []
      }
      this.isBackAction = false;
    }

    this.loading = false;
  }

  gotoCreatePromoForm() {
    this.utilsService.goToPage('/admin/edit-promocion', { company_id: this.selectedCompany[0].id })
    // this.utilsService.goToPage('/admin/edit-promocion', { companyId: this.selectedCompany })
  }

  limpiar() {
    this.renderList = [];
    this.selectedCompany = this.empresas[0].id;
  }

  goToEditPromo(item: any, promotion_id: number) {
    // this.promotionList.find((element: Promotion) => {
    //   if (element.id === item.id) {
    //     item = element;
    //   }
    // });

    // this.router.navigate(['/admin/edit-promocion'], {
    //   queryParams: { PromoModel: JSON.stringify(item) },
    // });

    // this.utilsService.goToPage('/admin/edit-promocion', { PromoModel: JSON.stringify(item) })
    this.utilsService.goToPage('/admin/edit-promocion', { promotion_id: promotion_id })
  }

  async eliminarPromo(item: any, promotion_id: string) {
    try {
      const response = await this.promotionService.deletePromotion(promotion_id);
      // const response = await this.promotionService.deletePromotion(item.id);
      if (response?.success === true) {
        this.utilsService.dialog(
          'SUCCESS',
          'Notificación',
          response?.message.description
        );
        this.loadCompanyPromotions();
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          'Por favor vuelve a intentar más tarde.'
        );
      }
      this.loadCompanyPromotions();
    } catch (error) {
      this.utilsService.dialog('ERROR', 'Error', 'No existen promociones');
    }
  }

  async updateStatusEvent($event: any, id: number) {
    const status = $event.target.value;
    try {
      const response = await this.promotionService.changePromoStatus(
        id,
        status
      );
      if (response?.success === true) {
        this.utilsService.dialog(
          'SUCCESS',
          'Notificación',
          response?.message.description
        );
        this.loadCompanyPromotions();
      } else {
        this.utilsService.dialog(
          'ERROR',
          'Error',
          response?.message.description ??
          'Por favor vuelve a intentar más tarde.'
        );
        this.loadCompanyPromotions();
      }
    } catch (error) {
      this.utilsService.dialog(
        'ERROR',
        'Error',
        'Por favor vuelve a intentar más tarde.'
      );
      this.loadCompanyPromotions();
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName)
  }

  onLeft() {
    if (this.pageNum + 1 != 1) {
      this.pageNum--;
      this.renderList.length = 0;
      this.loadCompanyPromotions();
    }
  }

  onRight() {
    if (this.pageNum + 1 != this.totalPages) {
      this.pageNum++;
      this.renderList.length = 0;
      this.loadCompanyPromotions();
    }
  }
}
