import { NgForm } from '@angular/forms';
import { listas } from 'src/app/models/back-service.model';
import { UserService } from 'src/app/services/user.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { UtilsService } from 'src/app/services/utils.service';
import { CompanyService } from 'src/app/services/company.service';
import { CommunicationChannelsResultModel } from 'src/app/models/communication-channels.model';

interface User {
  id: number;
  status: string;
  companyId: number;
  password: string;
  firstName: string;
  secondName: string;
  firstLastname: string;
  secondLastname: string;
  phone: number;
  roles: [any];
  roleId: number | null | string;
  puestoId: number | null
  gender_id: number | null
  email: string;
  emailAdditional: string;
  personalNIT: number;
  birthday: string;
  country_id: number | null;
  areaId: number | null;
  associated: boolean;
  // permissions: [any];
  permissions: string[];
  ownerContact: boolean;

  sectorId: number | null;
  comiteId: number | null;
  comisionId: number | null;
}

interface Company {
  nit: number | null;
  nombreComercial: string;
}
@Component({
  selector: 'app-edit-usuario',
  templateUrl: './edit-usuario.component.html',
  styleUrls: ['./edit-usuario.component.css']
})
export class EditUsuarioComponent implements OnInit {
  @ViewChild('form') form!: NgForm;
  public loading: boolean = false;

  public userID: number = 0;
  public jobList: listas[] = [];
  public roleList: listas[] = [];
  public areaList: listas[] = [];
  public user: User = {} as User;
  public sectorList: listas[] = [];
  public comiteList: listas[] = [];
  public countryList: listas[] = [];
  public comisionList: listas[] = [];
  public company: Company = {} as Company;
  public communicationsChannel: CommunicationChannelsResultModel = {} as CommunicationChannelsResultModel;

  // userData = new BackServiceUsers();
  // companyData = new BackServiceCompanies();
  // testPerm = "escanear eventos";
  // model!: NgbDateStruct;

  accessToken: any;
  permissions: any[] = []; // permissions: string[] = [];
  escanear_eventos: boolean = false;
  subir_promociones: boolean = false;
  descargar_reporte_asistente: boolean = false;
  constancia_asociado: boolean = false;
  certificado_puntos: boolean = false;

  constructor(
    private userService: UserService,
    private utilsService: UtilsService,
    private companyService: CompanyService
  ) { }

  async ngOnInit() {
    await this.loadLists();
    this.userID = this.utilsService.getParam('ID') || 0;

    if (sessionStorage.getItem('Token') && this.userID > 0) {
      // EDIT
      this.accessToken = sessionStorage.getItem('Token');
      this.loadDatabyID();
    }
    else if (sessionStorage.getItem('Token')) {
      // NEW
      this.user.roleId = 7;
      this.user.status = 'active';
      this.user.areaId = null;
      this.user.puestoId = null;
      this.user.comiteId = null;
      this.user.sectorId = null;
      this.user.gender_id = null;
      this.user.comisionId = null;
      this.user.country_id = null;
      this.user.roles = ['noasociado'];
      this.user.roleId = 1;
    }
    else {
      // NO ACCESS
      this.utilsService.dialog('ERROR', 'Error', 'No tienes acceso...');
      this.goToPage('/login');
    }
  }

  validation_msg(name: string): boolean {
    if (this.form && this.form.controls[name]) {
      if (
        (this.form.controls[name].errors || this.form.controls[name].invalid)
        &&
        (this.form.controls[name].dirty || this.form.controls[name].touched)
      ) {
        return true;
      } else {
        return false;
      }
    } else {
      return false;
    }
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  setPermissions() {
    this.escanear_eventos = this.permissions.includes('escanear_eventos');
    this.subir_promociones = this.permissions.includes('subir_promociones');
    this.certificado_puntos = this.permissions.includes('certificado_puntos');
    this.constancia_asociado = this.permissions.includes('constancia_asociado');
    this.descargar_reporte_asistente = this.permissions.includes('descargar_reporte');
  }

  onPermissionChange($event: any) {
    if ($event.target.name == 'escanear_eventos' && $event.target.checked) {
      this.permissions.push('escanear_eventos');
    } else if ($event.target.name == 'escanear_eventos' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('escanear_eventos'), 1);
    }
    if ($event.target.name == 'subir_promociones' && $event.target.checked) {
      this.permissions.push('subir_promociones');
    } else if ($event.target.name == 'subir_promociones' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('subir_promociones'), 1);
    }
    if ($event.target.name == 'descargar_reporte' && $event.target.checked) {
      this.permissions.push('descargar_reporte');
    } else if ($event.target.name == 'descargar_reporte' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('descargar_reporte'), 1);
    }
    if ($event.target.name == 'constancia_asociado' && $event.target.checked) {
      this.permissions.push('constancia_asociado');
    } else if ($event.target.name == 'constancia_asociado' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('constancia_asociado'), 1);
    }
    if ($event.target.name == 'certificado_puntos' && $event.target.checked) {
      this.permissions.push('certificado_puntos');
    } else if ($event.target.name == 'certificado_puntos' && !$event.target.checked) {
      this.permissions.splice(this.permissions.indexOf('certificado_puntos'), 1);
    }

  }

  changeCheck(event: any, type: string, value: boolean) {
    switch (type) {
      case 'owner':
        this.user.ownerContact = value;
        break;
      case 'associated':
        this.user.associated = value;
        break;
      default:
        break;
    }
  }

  loadLists() {
    this.utilsService.getCountries().then(
      response => {
        // console.log('countries: ', response);
        this.countryList = response.result.countries.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getPuestos().then(
      response => {
        // console.log('puesto: ', response);
        this.jobList = response.result.puestos.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getAreas().then(
      response => {
        // console.log('area: ', response);
        this.areaList = response.result.areas.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getRoles().then(
      response => {
        // console.log('role: ', response);
        this.roleList = response.result.roles.map((e: any) => {
          return { id: e?.id, key: e?.value, name: e?.label }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getCommissions().then(
      response => {
        // console.log('comission: ', response);
        this.comisionList = response.result.comisiones.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getCommittees().then(
      response => {
        // console.log('committees: ', response);
        this.comisionList = response.result.comites.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );

    this.utilsService.getSectors().then(
      response => {
        // console.log('sectors: ', response);
        this.sectorList = response.result.sectors.map((e: any) => {
          return { id: e?.id, name: e?.name }
        })
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );
  }

  async loadDatabyID() {
    this.loading = true;
    await this.userService.getOneUser(this.userID).then(
      response => {
        this.user.id = response.result?.id;
        this.user.phone = response.result?.phone;
        this.user.email = response.result?.email;
        this.user.puestoId = response.result?.puesto;
        this.user.areaId = response.result?.area || null;
        this.user.companyId = response.result?.companyId;
        this.user.firstName = response.result?.firstName;
        this.user.secondName = response.result?.secondName;
        this.user.personalNIT = response.result?.personalNIT;
        this.user.sectorId = response.result?.sector || null;
        this.user.comiteId = response.result?.comite || null;
        this.user.status = response.result?.status || 'unknown';
        this.user.comisionId = response.result?.comision || null;
        this.user.firstLastname = response.result?.firstLastname;
        this.user.gender_id = response.result?.gender?.id || null;
        this.user.secondLastname = response.result?.secondLastname;
        this.user.emailAdditional = response.result?.emailAdditional;
        this.user.country_id = response.result?.country?.id || null;
        this.user.associated = response.result?.associated || false;
        this.user.ownerContact = response.result?.ownerContact || false;
        this.user.birthday = response.result?.birthday.substring(0, 10) || '';

        this.user.roleId = response.result?.roleId || null;
        this.user.roles = [(response.result.role.toLowerCase()).slice(5)] || [];

        this.setPermissions();
        console.log(response.result);
        console.log(this.user);
      },
      error => {
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
      }
    ).finally(() => {
      this.loading = false;
    });

    this.utilsService.getCommunicationChannelsByUser(this.userID).then(
      response => {
        this.communicationsChannel = response.result;
      },
      error => this.utilsService.dialog('ERROR', 'Error', error.error.message.description)
    );
  }

  async submit() {
    if (this.form.valid) {
      this.loading = true;
      const role = this.roleList.find(x => x.id === Number(this.user.roleId));
      this.user.roles = [role!.key]
      this.userID > 0 ? this.editUser() : this.createUser();
      this.loading = false;
    } else {
      this.form.control.markAllAsTouched();
      this.utilsService.dialog('ERROR', 'Error', '¡Llena los campos obligatorios!');
    }
  }

  createUser() {
    //we need to remove static password when user is created
    this.user.password = "Aplus123"; // "pass123";
    this.user.permissions = this.permissions || [];
    console.log('save', this.user, this.permissions);

    this.loading = true;
    this.userService.createUser(this.user).then(
      response => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Éxito', response.message.description, '/admin/users/admin-usuarios');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
      }
    ).finally(() => {
      this.loading = false;
    })
  }

  editUser() {
    this.user.permissions = this.permissions || [];
    console.log('edit', this.user, this.permissions);

    this.loading = true;
    this.userService.updateUser(this.userID, this.user).then(
      response => {
        console.log(response);
        this.utilsService.dialog('SUCCESS', 'Éxito', response.message.description, '/admin/users/admin-usuarios');
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
      }
    ).finally(() => {
      this.loading = false;
    })
  }

  recoverPassword() {
    this.utilsService.dialog('SUCCESS', 'Test', 'Link enviado al correo.');
    // this.loading = true;
    // this.userService.recoverPassword({ username: this.userData.email }).then(
    //   response => {
    //     console.log(response);
    //     this.utilsService.dialog('SUCCESS', 'Éxito', response.message.description);
    //   },
    //   error => {
    //     console.log(error);
    //     this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
    //   }
    // ).finally(() => {
    //   this.loading = false;
    // })
  }

  searchCompany(company_id: number | null) {
    this.loading = true;
    this.companyService.getCompaniesbyID(1).then(
      response => {
        console.log(response);
        // this.user.companyId = response.result.id;
        this.company.nombreComercial = response.result.nombreComercial;
      },
      error => {
        console.log(error);
        this.utilsService.dialog('ERROR', 'Error', error.error.message.description);
      }
    ).finally(() => {
      this.loading = false;
    })
  }
}