import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';
import { PromotionCreateModel, PromotionResponse, RolEntity, promotionCategorie } from '../models/promotions-models';
import { GenericApiResponse } from '../models/generic-api-response.model';

@Injectable({
  providedIn: 'root'
})
export class PromotionsService {
  private headers: HttpHeaders;
  private url: string = environment.url;
  private accessToken: string | undefined;

  constructor(private http: HttpClient) {
    this.accessToken = sessionStorage.getItem('Token')?.toString();

    this.headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'accept': 'application/json',
      'x-access-token': this.accessToken!
    });
  }

  getOnePromotions(promotion_id: number): Promise<any> {
    const options = { headers: this.headers };
    return this.http.get(`${this.url}/promo/promo/get/${promotion_id}`, options).toPromise();
  }

  getAllPromotions(status: string, name: string | null = null, page: number | null = null, size: number | null = null, startDate: string | null = null, endDate: string | null = null, companyId: string | null = null): Promise<PromotionResponse | undefined> {
    let params = new HttpParams();
    (name) && (params = params.set('name', name));
    (page) && (params = params.set('page', page));
    (size) && (params = params.set('size', size));
    (status) && (params = params.set('status', status));
    (endDate) && (params = params.set('end_at', endDate));
    (startDate) && (params = params.set('start_at', startDate));
    (companyId) && (params = params.set('company_id', companyId));
    const options = { headers: this.headers, params: params };
    
    return this.http.get<PromotionResponse>(`${this.url}promo/promo/for-admin`, options).toPromise();
  }

  // Reporte
  getPromotionsByUser(promotion_id: number | null = null, startDate: string | null = null, endDate: string | null = null, page: number | null = null, size: number | null = null,  status: string): Promise<PromotionResponse | undefined> {
    // let params = new HttpParams();
    
    let params = new HttpParams();
    (promotion_id) && (params = params.set('promotionId', promotion_id));
    (startDate) && (params = params.set('startDate', startDate));
    (endDate) && (params = params.set('endDate', endDate));
    (page) && (params = params.set('page', page));
    (size) && (params = params.set('size', size));
    (status) && (params = params.set('status', status));
    // (name) && (params = params.set('name', name));    
    
    // http://agexportplus.dev.tribalworldwide.gt/api/v1/promo/promo/report-redeems?promotionId=41&startDate=2023-03-23&endDate=2023-06-30&page=0&size=10&status=used'
    const options = { headers: this.headers, params: params };
    return this.http.get<PromotionResponse>(`${this.url}promo/promo/report-redeems`, options).toPromise();
    // return this.http.get<PromotionResponse>(`${this.url}promo/promo/with-users/${user_id}`, options).toPromise(); user_id: number -> Cambio para utilizar la paginación
  }

  
  getAllPromotionsCommercialRole(status: string, name: string | null = null, page: number | null = null, size: number | null = null, startDate: string | null = null, endDate: string | null = null, companyId: number | null = null): Promise<PromotionResponse | undefined> {
    const params = new HttpParams()
      .set('page', page!.toString())
      .set('size', size!.toString())
      .set('start_at', startDate ?? '')
      .set('end_at', endDate ?? '')
      .set('status', status)
      .set('company_id', companyId ?? '')

      const options = { headers: this.headers, params: params };

    return this.http.get<PromotionResponse>(`${this.url}promo/promo/by-company/${companyId}`, options).toPromise();
  }

  deletePromotion(id: string): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.post<GenericApiResponse<any>>(`${this.url}promo/promo/delete/${id}`, {}, options).toPromise();
  }

  createPromotionByComercialRole(promotion: PromotionCreateModel): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.post<GenericApiResponse<any>>(`${this.url}promo/promo/create`, promotion, options).toPromise();
  }

  createPromotionByAdminRole(promotion: PromotionCreateModel): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.post<GenericApiResponse<any>>(`${this.url}promo/promo/create-by-admin`, promotion, options).toPromise();
  }

  updatePromotion(promotion: PromotionCreateModel): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.post<GenericApiResponse<any>>(`${this.url}promo/promo/update/${promotion.id}`, promotion, options).toPromise();
  }

  getPromoCategories(): Promise<GenericApiResponse<promotionCategorie[]> | undefined> {
    const options = { headers: this.headers };
    return this.http.get<GenericApiResponse<promotionCategorie[]>>(`${this.url}/promo/promo-categories/all-active`, options).toPromise();
  }

  getPromoLimitsPerUser(): Promise<GenericApiResponse<number[]> | undefined> {
    const options = { headers: this.headers };
    return this.http.get<GenericApiResponse<number[]>>(`${this.url}promo/promo/promo-limits`, options).toPromise();
  }

  getRoles(): Promise<GenericApiResponse<RolEntity[]> | undefined> {
    const options = { headers: this.headers };
    return this.http.get<GenericApiResponse<RolEntity[]>>(`${this.url}registro/roles`, options).toPromise();
  }

  changePromoStatus(id: number, status: string): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.post<GenericApiResponse<any>>(`${this.url}promo/promo/change-status/${id}`, { 'status': status }, options).toPromise();
  }

  getQRData(id: number): Promise<GenericApiResponse<any> | undefined> {
    const options = { headers: this.headers };
    return this.http.get<GenericApiResponse<any>>(`${this.url}promo/promo//generate-qr/${id}`, options).toPromise();
  }

}
