import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { KeyValue } from '@angular/common';
import { UtilsService } from 'src/app/services/utils.service';
import { EventService } from 'src/app/services/event.service';
import { NotificationItem } from 'src/app/models/notification.item.model';
import { NotificationsService } from 'src/app/services/notifications.service';

@Component({
  selector: 'app-admin-notificaciones',
  templateUrl: './admin-notificaciones.component.html',
  styleUrls: ['./admin-notificaciones.component.css'],
})
export class AdminNotificacionesComponent implements OnInit {
  constructor(
    private router: Router,
    private notificationsService: NotificationsService,
    private utilsService: UtilsService,
    private eventService: EventService
  ) {
    this.categoriesHeading = Object.keys(this.listHeading[0]);
  }

  public nextId = 1;
  public pageNum = 0;
  public pageSize = 20;
  public totalPages = 0;
  public filtro: string = '';
  public renderList: any[] = [];
  public loading: boolean = false;
  public isShowDiv: boolean = true;
  public eventAndPromoList: any[] = [];
  public categoriesHeading: string[] = [];
  public notificationsList: NotificationItem[] = [];

  public listHeading: {}[] = [
    {
      'Nombre de la notificación': '',
      'Fecha de creación': '',
      'Fecha de envío': '',
      Evento: '',
      'Usuarios Enviados': '',
      Estatus: '',
      '': '',
    },
  ];

  originalOrder = (
    a: KeyValue<string, string>,
    b: KeyValue<string, string>
  ): number => {
    return 0;
  };

  ngOnInit(): void {
    this.loadActiveEvents();
  }

  public getTypeOf(variable: any): string {
    return typeof variable;
  }

  async loadNotifications() {
    this.loading = true;
    this.renderList = [];
    try {
      const response = await this.notificationsService.getAllNotifications();
      // const response = await this.notificationsService.getAllNotificationsDennis(this.pageNum, this.pageSize);
      if (response?.success === true) {
        // console.log(response.result)

        this.notificationsList = response.result;
        this.notificationsList = this.notificationsList.reverse();

        this.renderList = response.result.map((e) => {
          return {
            'Nombre de la notificación': e.subject,
            'Fecha de creación': e.createdAt ?? '',
            'Fecha de envío': e.dateSend ?? '',
            Evento: this.getEventName(e.eventId) == '' ? 'Evento no existente' : this.getEventName(e.eventId), 'Usuarios enviados': '123',
            Estatus: this.getNotificationStatus(e.notificationType),
            id: e.id
          };
        });

        this.totalPages = Math.ceil(response.result.length / this.pageSize); // response.count
        // console.log(this.totalPages)
      } else {
        this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
      }
    } catch (error) {
      this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
    } finally {
      this.loading = false;
    }
  }

  getEventName(id: number) {
    const event = this.eventAndPromoList.find((e) => e.id === id);
    return event ? event.name : '';
  }

  getNotificationStatus(status: string) {
    switch (status) {
      case 'sent':
        return 'Enviado';
        break;
      case 'draft':
        return 'Borrador';
      case 'scheduled':
        return 'Programado';
        break;
      default:
        return '';
        break;
    }
  }

  async loadActiveEvents() {
    this.eventService.getEvents(null, null, null).then(
      (response) => {
        response.result.map((e: any) => {
          if (e.status === 'active') {
            this.eventAndPromoList.push({
              id: e.id,
              name: e.title,
              type: 'event',
            });
          }
        });
        this.loadNotifications();
      },
      error => this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.')
    );
  }

  showActions(event: any, index: number) {
    this.isShowDiv = true;
    var element = document.getElementById('item' + index);
    var listItes = document.getElementsByClassName('actions-disble')[0];
    if (listItes) {
      listItes.classList.remove('actions-disble');
    }
    if ('item' + index !== listItes?.id || !listItes.id) {
      element?.classList.add('actions-disble');
    }
  }

  async deleteNotification(notification: any) {
    try {
      const response = await this.notificationsService.deleteNotification(notification.id);
      console.log(response)
      if (response?.success === true) {
        // : CAMBIAR ESTA CONDICIÓN CUANDO SE PUEDA ELIMINAR UNA NOTIFICACIÓN O BACK RETORNE UN MENSAJE DE ERROR O CÓDIGO
        if (response?.message?.description === 'Una notificacion enviada no se puede modificar o eliminar') {
          this.utilsService.dialog('ERROR', 'Error', 'Una notificación enviada no se puede modificar o eliminar.');
          return;
        } else {
          this.utilsService.dialog('SUCCESS', 'Notificación', response?.message.description);
        }
      } else {
        this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
      }
    } catch (error) {
      this.utilsService.dialog('ERROR', 'Error', 'Por favor vuelve a intentar más tarde.');
    }

    this.loadNotifications();
  }

  editNotification(index: number) {
    console.log(this.notificationsList[index])
    const notificationModel = this.notificationsList[index];
    this.utilsService.goToPage('/admin/edit-notificacion', { ID: notificationModel.id });
  }

  goToPage(pageName: string) {
    this.utilsService.goToPage(pageName);
  }

  onLeft() {
    if (this.pageNum + 1 != 1) {
      this.pageNum--;
      this.notificationsList.length = 0;
      this.loadNotifications();
    }
  }

  onRight() {
    if (this.pageNum + 1 != this.totalPages) {
      this.pageNum++;
      this.notificationsList.length = 0;
      this.loadNotifications();
    }
  }
}
