import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { MediaMatcher } from '@angular/cdk/layout';
import { UtilsService } from '../services/utils.service';
import { Router, NavigationStart } from '@angular/router';
import { interval } from 'rxjs/internal/observable/interval';


interface Menu {
  id: string,
  src: string,
  name: string,
  routerLink: string
}

@Component({
  selector: 'app-main-menu',
  templateUrl: './main-menu.component.html',
  styleUrls: ['./main-menu.component.css'],
})
export class MainMenuComponent implements OnInit {
  public showFiller = false;
  // public mediaQueryListSmall;
  // public mediaQueryListLarge;
  private subscription: any;
  private secondsCounter$: any;
  public main_menu: Menu[] = [];
  public showHead: boolean = false;

  constructor(
    private router: Router,
    public mediaMatcher: MediaMatcher,
    private utilsService: UtilsService,
    private changeDetector: ChangeDetectorRef
  ) {
    // this.mediaQueryListSmall = mediaMatcher.matchMedia('(max-width: 959.99px)');
    // this.mediaQueryListLarge = mediaMatcher.matchMedia('(min-width: 959.99px)');

    // on route change to '/login', set the variable showHead to false
    router.events.forEach((event) => {
      if (event instanceof NavigationStart) {
        if (
          event['url'] == '/login' ||
          event['url'] == '/recovery' ||
          event['url'] == '/code-recovery' ||
          event['url'] == '/new-password'
        ) {
          this.showHead = false;
        } else {
          this.showHead = true;
        }
      }
    });
  }

  changeSidebar(web: boolean, movil: boolean) {
    console.log('web: ' + web, 'movil: ' + movil);
  }

  ngOnInit(): void {
    this.validation();

    const userRole: string | boolean = sessionStorage.getItem('Role') || false;

    if (userRole === 'ROLE_SUPERADMIN' || userRole === 'ROLE_ADMIN') {
      this.main_menu = [
        {
          id: 'users',
          routerLink: 'users', // 'users/admin-empresas', -> llega con ambos activos pero luego pierde el active del padre
          name: 'Administración de usuarios',
          src: '../../assets/images/UsersIcon.svg',
        },
        {
          id: 'evento',
          name: 'Eventos ',
          routerLink: 'events', // 'events/calendario-eventos', -> llega con ambos activos pero luego pierde el active del padre
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          name: 'Estadisticas',
          routerLink: 'reportes',
          src: '../../assets/images/ReporteIcon.svg'
        },
        {
          id: 'notificaciones',
          name: 'Notificaciones',
          routerLink: 'notificaciones',
          src: '../../assets/images/NotificacionIcon.svg'
        },
        {
          id: 'promociones',
          name: 'Beneficios',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    } else if (userRole === 'ROLE_COLABORADOR') {
      this.main_menu = [
        {
          id: 'users',
          routerLink: 'users', // 'users/admin-empresas', -> llega con ambos activos pero luego pierde el active del padre
          name: 'Administración de usuarios',
          src: '../../assets/images/UsersIcon.svg',
        },
        {
          id: 'evento',
          name: 'Eventos ',
          routerLink: 'events', // 'events/calendario-eventos', -> llega con ambos activos pero luego pierde el active del padre
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          name: 'Estadisticas',
          routerLink: 'reportes',
          src: '../../assets/images/ReporteIcon.svg'
        },
        {
          id: 'promociones',
          name: 'Beneficios',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    } else if (userRole === 'ROLE_ORGANIZADOR_EVENTOS') {
      this.main_menu = [
        {
          id: 'evento',
          routerLink: 'events/calendario-eventos',
          name: 'Eventos',
          src: '../../assets/images/EventoIcon.svg'
        },
        {
          id: 'reportes',
          routerLink: 'reportes',
          name: '',
          src: '../../assets/images/ReporteIcon.svg'
        }
      ]
    } else {
      // ROLE_ALIADO 
      this.main_menu = [
        {
          id: 'promociones',
          name: 'Beneficios',
          routerLink: 'admin-promociones',
          src: '../../assets/images/promotion_icon.svg'
        }
      ]
    }
  }

  validation() {
    this.secondsCounter$ = interval(1200000); //15seg. = 15000
    this.subscription = this.secondsCounter$.subscribe(() => {
      const current = new Date().getTime();
      const getSession = localStorage.getItem('session')?.toString() || '';
      const expiration = new Date(parseInt(getSession.toString())).getTime();

      if (getSession === '' || (current >= expiration)) {
        this.logout();
        this.utilsService.dialog('ERROR', 'Error', '¡Sesión expirada!');
      }
    });
  }

  ngAfterContentChecked(): void {
    // this.mediaQueryListSmall = this.mediaMatcher.matchMedia('(max-width: 959.99px)');
    // this.mediaQueryListLarge = this.mediaMatcher.matchMedia('(min-width: 959.99px)');
    // this.changeSidebar(this.mediaQueryListSmall.matches, this.mediaQueryListLarge.matches);

    //////M1
    // const layoutChanges = breakpointObserver.observe(['(orientation: portrait)', '(orientation: landscape)' ]);
    // layoutChanges.subscribe(result => { updateMyLayoutForOrientationChange(); });
    //////M2
    // const isSmallScreen = breakpointObserver.isMatched('(max-width: 599px)');    
    //////M3
    // this.breakpointObserver.observe([Breakpoints.XSmall, Breakpoints.Large, Breakpoints.Medium, Breakpoints.Small])
    // .subscribe(result => {
    //   console.log('XSmall: ', result.breakpoints['(max-width: 599.99px)']); console.log('Small: ', result.breakpoints['(min-width: 600px) and (max-width: 959.99px)']);
    //   console.log('Medium: ', result.breakpoints['(min-width: 960px) and (max-width: 1279.99px)']); console.log('Large: ', result.breakpoints['(min-width: 1280px) and (max-width: 1919.99px)']);
    //   if (result.matches) { this.activateHandsetLayout('grande'); })
    this.changeDetector.detectChanges();
  }

  logout() {
    sessionStorage.removeItem('Token');
    sessionStorage.clear();
    localStorage.clear();

    this.subscription.unsubscribe();
    this.router.navigate([`/login`]);
  }
}
